<template>
	<div class="payment">
		<!-- 头部菜单选项卡 -->
		<div class="padding-top-20 padding-horizontal-20">
			<div class="page-header">
				<ul class="nav-left">
					<li v-for="(item,index) in menuList" :key="index" :class="menuIndex.nav == index ? 'active':''" @click="cliMenuList(index)">{{item}}</li>
				</ul>
				<div class="nav-right">
					<div class="nav-btn-left">
						<div class="nav-btn-img">
							<svg class="icon" aria-hidden="true">
								<use xlink:href="#icon-list"></use>
							</svg>
						</div>
						<div class="nav-btn-img">
							<svg class="icon" aria-hidden="true">
								<use xlink:href="#icon-icon-clock"></use>
							</svg>
						</div>
					</div>
					<div class="nav-btn-right" @click="close = 'flex'">{{menuIndex.nav == 0 ? '创建款项记录':'创建票据记录'}}</div>
				</div>
			</div>
			<!-- 内容主体 -->
			<div class="page-main">
				<!-- 选择组 -->
				<div class="page-main-menu">
					<ul class="menu-list">
						<li class="menu-li" v-for="(item,index) in menuData" :key="index">
							<span>{{item.title}}</span>
							<ul class="menu-li-list">
								<li :class="menuIndex['li'+index] == inde ? 'active':''" v-for="(ite,inde) in item.list" :key="inde" @click="cliMenuLi(index,inde)">{{ite}}</li>
							</ul>
						</li>
					</ul>
					<div class="menu-input">
						<div class="menu-input-li">
							<span>所属客户</span>
							<input placeholder="请输入客户名检索" />
							<span style="padding-left: 20px;">所属合同</span>
							<input placeholder="请输入合同检索" />
						</div>
						<div class="menu-input-li">
							<span>所属部门</span>
							<input placeholder="请输入所属部门" />
							<span style="padding-left: 20px;">负责人</span>
							<input placeholder="请输入负责人名字" />
						</div>
					</div>
				</div>
			</div>
		</div>
		<!-- 表格 -->
		<div class="padding-top-20 padding-horizontal-20">
			<div class="table-header">
				<div></div>
				<Search :label="menuIndex.nav == 0 ? '款项名称':'票据内容'" @searFunc="getSearchV"></Search>
			</div>
			<div class="margin-top-20">
				<Table :tableData='tableData' :search="search" :newTrData="newTrData" @tableFun="tableFun"></Table>
			</div>
		</div>
		<!--创建合同-->
		<div class="modal" :style="'display:'+close">
			<FormCreate :formData="formData" @closeFunc='closeFunc'></FormCreate>
		</div>
	</div>
</template>

<script>
	import Search from '../components/Search'
	import Table from '../components/Table'
	import FormCreate from '../components/FormCreate'
	export default {
		components: {
			Search,
			Table,
			FormCreate
		},
		data: function() {
			return {
				newTrData:'', // 新分页数据
				close: 'none', //创建模板的显示状态
				search: '', //搜索框内容
				menuList: ['款项记录', '票据记录'], //菜单选项卡标题
				menuData: [ //菜单选项卡数据
					{
						"title": '款项',
						'list': ['全部款项', '共享给我的款项', '我共享的款项']
					},
					{
						"title": '款项类型',
						'list': ['全部', '预付款', '合同款', '尾款', '未分类']
					}, {
						"title": '回款时间',
						'list': ['全部', '昨天', '今天', '本周', '上周', '本月', '上月']
					}, {
						"title": '关联合同',
						'list': ['全部', '未关联合同', '已关联合同']
					}, {
						"title": '审批状态',
						'list': ['全部', '已审批', '审批中']
					}
				],
				menuIndex: {
					'nav': 0,
					'li0': 0,
					'li1': 0,
					'li2': 0,
					'li3': 0,
					'li4': 0,
				}, //菜单选项卡的下标
				tableData: { //表格数据
					thData: [{
							name: '编号',
							key: 'id'
						},
						{
							name: '任务内容',
							key: 'address'
						},
						{
							name: '任务负责人',
							key: 'name'
						}, {
							name: '开始时间',
							key: 'date'
						}
					],
					trData: [{
						id: 0,
						date: '2016-05-03',
						name: '王',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 1,
						date: '2016-05-02',
						name: '小',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 2,
						date: '2016-05-04',
						name: '虎',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 3,
						date: '2016-05-01',
						name: '王小虎',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 4,
						date: '2016-05-08',
						name: '王小虎',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 5,
						date: '2016-05-06',
						name: '王小虎',
						address: '上海市普陀区金沙江路 1518 弄'
					}, {
						id: 6,
						date: '2016-05-07',
						name: '王小虎',
						address: '上海市普陀区金沙江路 1518 弄'
					}]
				},
				formData: { //创建模板数据
					title: '创建款项记录',
					group: [{
							label: '名称(*)',
							required: true,
						},
						{
							label: '所属客户(*)',
							required: true,
						}, {
							label: '所属合同',
							required: false,
						}, {
							label: '款项类型',
							required: false,
						}, {
							label: '回款金额(*)',
							required: true,
						}, {
							label: '回款日期(*)',
							required: true,
						}, {
							label: '负责人',
							required: false,
						}
					],
				},
			}
		},
		methods: {
			tableFun: function(data){ //点击翻页时获取数据
				this.newTrData = data;
			},
			cliMenuLi: function(index, inde) { //点击选项单元
				this.menuIndex['li' + index] = inde;
			},
			cliMenuList: function(index) { //点击菜单选项卡
				this.menuIndex.nav = index;
			},
			getSearchV: function(data) { //获取搜索子组件内容
				this.search = data;
			},
			closeFunc(data) { //获取子组件创建模板的状态
				this.close = data;
			},
		},
		watch: {
			'menuIndex.nav': function(newI) { //监听菜单选项卡的切换，改变选项组内容
				if (newI == 0) {
					this.menuData.splice(0, this.menuData.length, {
						"title": '款项',
						'list': ['全部款项', '共享给我的款项', '我共享的款项']
					}, {
						"title": '款项类型',
						'list': ['全部', '预付款', '合同款', '尾款', '未分类']
					}, {
						"title": '回款时间',
						'list': ['全部', '昨天', '今天', '本周', '上周', '本月', '上月']
					}, {
						"title": '关联合同',
						'list': ['全部', '未关联合同', '已关联合同']
					}, {
						"title": '审批状态',
						'list': ['全部', '已审批', '审批中']
					})
					for (let i = 0; i < this.menuData.length; i++) { //选项组状态归零
						this.menuIndex['li' + i] = 0
					}
					this.tableData.th = ['名称', '所属客户', '回款日期', '回款金融', '类型', '收款人', '关联合同', '创建时间', '更新日期'];
					this.tableData.loading = '没有找到相关回款记录';
					this.formData.title = '创建款项记录';
					this.formData.group = [{
								label: '名称(*)',
								required: true,
							},
							{
								label: '所属客户(*)',
								required: true,
							}, {
								label: '所属合同',
								required: false,
							}, {
								label: '款项类型',
								required: false,
							}, {
								label: '回款金额(*)',
								required: true,
							}, {
								label: '回款日期(*)',
								required: true,
							}, {
								label: '负责人',
								required: false,
							}
						];
				} else if (newI == 1) {
					this.menuData.splice(0, this.menuData.length, {
						"title": '票据',
						'list': ['全部票据', '共享给我的票据', '我共享的票据']
					}, {
						"title": '票据类型',
						'list': ['全部', '发票', '收据', '未分类']
					}, {
						"title": '开票时间',
						'list': ['全部', '昨天', '今天', '本周', '上周', '本月', '上月']
					});
					for (let i = 0; i < this.menuData.length; i++) { //选项组状态归零
						this.menuIndex['li' + i] = 0
					}
					this.tableData.th = ['票据内容', '所属客户', '开票日期', '票据金融', '类型', '开票人', '关联合同', '快递单号','创建时间', '更新时间'];
					this.tableData.loading = '没有找到相关票据记录';
					this.formData.title = '创建票据记录';
					this.formData.group = [{
								label: '开票内容(*)',
								required: true,
							},
							{
								label: '开票金额(*)',
								required: true,
							}, {
								label: '开票日期(*)',
								required: true,
							}, {
								label: '票据类型',
								required: false,
							}, {
								label: '所属客户(*)',
								required: true,
							}, {
								label: '所属合同(*)',
								required: true,
							}, {
								label: '票据编号',
								required: false,
							},{
								label: '快递单号',
								required: false,
							},{
								label: '收件人',
								required: false,
							},{
								label: '电话',
								required: false,
							},{
								label: '开票人',
								required: false,
							}
						];
				}
			},
		},
		created() {
			this.newTrData = this.tableData.trData.slice(0, 3)
		},
	}
</script>

<style scoped>
	.payment {
		margin: 20px;
	}
/* 头部菜单选项卡 */
	.page-header {
		display: flex;
		justify-content: space-between;
		border-bottom: 1px solid #ddd;
		align-items: center;
	}

	.nav-left {
		display: flex;
	}

	.nav-left li {
		padding: 10px 20px;
		margin-right: 2px;
		cursor: pointer;
	}

	.page-header .nav-left .active {
		color: #3f51b5;
		background-color: transparent;
		border-bottom: 2px solid #3f51b5;
		cursor: default;
	}

	.nav-left li:hover {
		border-bottom: solid 2px #ccc;
	}

	.nav-right {
		display: flex;
		height: 33px;
	}

	.nav-btn-left {
		display: flex;
		width: 67px;
		height: 33px;
		text-align: center;
		line-height: 33px;
	}

	.nav-btn-img {
		width: 50%;
		height: 100%;
		text-align: center;
		line-height: 33px;
		cursor: pointer;
		background: #00bcd4;
	}

	.nav-btn-img :hover {
		opacity: .5;
	}

	.nav-btn-left .icon {
		width: 16px;
		height: 16px;
		color: #fff;
	}

	.nav-btn-right {
		height: 33px;
		margin-left: 5px;
		text-align: center;
		background-color: #5cb85c;
		padding: 6px 13px;
		color: #fff;
		cursor: pointer;
		border-radius: 5px;
	}
/* 内容主体 */
	.page-main {
		display: flex;
		padding-top: 20px;
	}

	/* 选项组 */
	.menu-li {
		display: flex;
		padding-bottom: 5px;
	}

	.menu-li span,
	.menu-input span {
		display: inline-block;
		width: 100px;
		padding: 5px;
	}

	.menu-li-list {
		display: flex;
	}

	.menu-li-list li {
		margin-right: 5px;
		padding: 5px 15px;
		color: #06C;
		cursor: pointer;
	}

	.menu-li-list .active {
		background-color: #00bcd4;
		color: #fff;
		border-radius: 2px;
	}

	.menu-input input {
		font-size: 14px;
		color: #555555;
		background-color: #fff;
		border: 1px solid #cccccc;
		border-radius: 2px;
		outline: 0;
		width: 350px;
		height: 36px;
		padding: 6px 27px 6px 15px;
		line-height: 1.57142857;
	}

	.menu-input-li {
		margin-bottom: 5px;
	}
	/* 表单 */
	.table-header {
		display: flex;
		justify-content: space-between;
	}
	
	/*创建模板*/
	.modal {
		position: fixed;
		left: 0;
		top: 0;
		z-index: 99;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		background: rgba(0, 0, 0, .5);
		transition: all .1s ease;
	}
</style>
